import { Component, OnInit, ViewChildren, QueryList } from '@angular/core';
import { faPlus, faPencilAlt, faTrashAlt, faSearch, faSort } from '@fortawesome/free-solid-svg-icons';
import { Observable } from 'rxjs';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { ActivatedRoute, Router } from '@angular/router';
import { EmpresasService } from '../services/empresas.service';
import { Empresa } from '../models/empresa.model';
import { NgbdSortableHeader, SortEvent } from '../directives/sortable.directive';

@Component({
  selector: 'empresas-list',
  templateUrl: './empresas-list.component.html',
  styleUrls: ['../empresas.component.scss']
})
export class EmpresasListComponent implements OnInit {

  title = '';
  resourceName = '/';
  faPlus = faPlus;
  faPencilAlt = faPencilAlt;
  faTrashAlt = faTrashAlt;
  faSearch = faSearch;
  faSort = faSort; 
  total$: Observable<number>;
  empresas$: Observable<Empresa[]>;
  static LOCALE_KEY = 'currentLocale';

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  constructor(private route: ActivatedRoute,
              private router: Router,
              private empresaService: EmpresasService,
              private toastr: ToastrService,
              public translate:TranslateService) { 

    this.empresas$ = empresaService.empresas$;
    this.total$ = empresaService.total$;
    this.resourceName = '/external-'+ this.empresaService.getResourceName();

    translate.setDefaultLang('ptBR');
    const currentLocale =  sessionStorage.getItem(EmpresasListComponent.LOCALE_KEY);
        
    translate.setDefaultLang(currentLocale);
  }

  get page() { return this.empresaService.page; }
  get pageSize() { return this.empresaService.pageSize; }
  get searchTerm() { return this.empresaService.searchTerm; }
  set page(page: number) { this.empresaService.page =  page;}
  set pageSize(pageSize: number) { this.empresaService.pageSize =  pageSize; }
  set searchTerm(searchTerm: string) {  this.empresaService.searchTerm = searchTerm; }

  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    this.empresaService.sortColumn = column;
    this.empresaService.sortDirection = direction;
  }

  ngOnInit() {
    this.empresaService.loadEmpresas();
  }
  
  create(): void {
    this.router.navigate([this.resourceName, 'create']);
  }

  change(indexId:string): void {
    this.router.navigate([this.resourceName, 'change', indexId]);
  }

  remove(indexId:string): void {
    this.router.navigate([this.resourceName, 'remove', indexId]);
  }

  clear(): void {
    this.searchTerm = "";
    this.headers.forEach(header => {
        header.direction = '';
    });
    this.empresaService.loadEmpresas();
  }

}
