import { Injectable, Inject, PipeTransform } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { DecimalPipe } from '@angular/common';

import { MessageService } from '../services/message.service';
import { Empresa } from '../models/empresa.model';
//import { Observable, of } from 'rxjs';
//import { catchError, map, tap } from 'rxjs/operators';
import { BehaviorSubject, Observable, of, Subject, throwError } from 'rxjs';
import { catchError, debounceTime, delay, finalize, switchMap, tap } from 'rxjs/operators';

import { SortDirection } from '../directives/sortable.directive';
import { ToastrService } from 'ngx-toastr';
import { environment } from '../../environments/environment';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

interface SearchResult {
  empresas: Empresa[];
  total: number;
}

interface State {
  page: number;
  pageSize: number;
  searchTerm: string;
  sortColumn: string;
  sortDirection: SortDirection;
}

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}


function sort(empresas: Empresa[], column: string, direction: string): Empresa[] {
  if (direction === '') {
    return empresas;
  } else {
    return [...empresas].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(empresa: Empresa, text: string, pipe: PipeTransform) {
  if (text===undefined || empresa == undefined || empresa == null) 
    return false; 
  const term = text.toUpperCase();
  return empresa.nomeEmpresa.toUpperCase().includes(term)
    || pipe.transform(empresa.codEmpresa).includes(term)
    || empresa.cnpj.toUpperCase().includes(term)
    || empresa.numBanco.includes(term);
}

function fullMatch(empresa: any, codEmpresa: string) {
  let item: Empresa = empresa as Empresa;
  return (item.codEmpresa.toString() === codEmpresa);
}


@Injectable({
  providedIn: 'root'
})
export class EmpresasService {
  private _serviceName = "EmpresaBoleto";
  private listaEmpresas: Empresa[] = [];
  private _loading$ = new BehaviorSubject<boolean>(false);
  private _search$ = new Subject<void>();
  private _empresas$ = new BehaviorSubject<Empresa[]>([]);
  private _total$ = new BehaviorSubject<number>(0);

  private _state: State = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  private originalCopy: Empresa = null;

  constructor(private http: HttpClient,
    private messageService: MessageService,
    private pipe: DecimalPipe,
    private toastr: ToastrService) {
    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._search()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._empresas$.next(result.empresas);
      this._total$.next(result.total);
    });
  }

  get empresas$() { return this._empresas$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }

  getResourceName(): string {
    return 'empresas';
  }

  validItem(codEmpresa: string) {
    let items = this.listaEmpresas.filter(item => fullMatch(item, codEmpresa));
    if (items.length > 0) {
      throwError(`JÃ¡ existe uma empresa com este cÃ³digo`);
    }
    console.log(items.length);
    return items.length;
  }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }

  private _search(): Observable<SearchResult> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm } = this._state;

    // 1. sort
    let empresas = sort(this.listaEmpresas, sortColumn, sortDirection);

    // 2. filter
    empresas = empresas.filter(empresa => matches(empresa, searchTerm, this.pipe));
    const total = empresas.length;

    // 3. paginate
    empresas = empresas.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    this.messageService.add("success", "EmpresaService: Lista de empresas selecionadas");

    return of({ empresas, total });

  }

  private log(message: string) {
    this.messageService.add("info", `EmpresaService: ${message}`);
  }

  loadEmpresas(): void {
    this.getEmpresas().subscribe(empresas => {
      this.listaEmpresas = empresas;
      this._empresas$.next(empresas);
      this._search$.next();
    });
  }

  getEmpresas(): Observable<Empresa[]> {
    this._loading$.next(true);
    console.log(this._serviceName + " Lista de items selecionados");
    this.messageService.add("info", this._serviceName + ": Lista de empresas selecionadas");

    return this.http.get<Empresa[]>(`${environment.serviceUrls.serviceUrl}/getAll`)
      .pipe(
        finalize(() => this._loading$.next(false)),
        catchError(this.handleError('getEmpresas', []))
      );

  }

  loadDynamicForm() {
    return this.http.get(`${environment.serviceUrls.serviceUrl}/getDynamicForm`)
      .pipe(tap(_ => this.log('fetched empresas')),
        catchError(this.handleError('getEmpresas', []))
      );
  }

  startNewItem(): Observable<Empresa> {
    let selectedItem: Empresa = new Empresa();
    this.originalCopy = null;
    //this._selectedItem$.next(this.selectedItem);
    return of(selectedItem);
  }

  selectItem(codEmpresa: string): Observable<Empresa> {
    if (codEmpresa === null || codEmpresa === undefined) {
      console.log(`${this._serviceName}: Empresa selecionada Ã© invÃ¡lido!`);
      let msg = `Empresa selecionada Ã© invÃ¡lida!`;
      this.toastr.error(msg, "CyberMessenger");
      return of(null);
    }
    let selectedItem: Empresa = this.listaEmpresas.find(item => fullMatch(item, codEmpresa));
    this.originalCopy = JSON.parse(JSON.stringify(selectedItem));
    //this._selectedItem$.next(this.selectedItem);
    return of(selectedItem);
  }

  rollbackChanges() {
    if (this.originalCopy === null) return;
    let selectedIndex = this.listaEmpresas.findIndex(item => fullMatch(item, this.originalCopy.codEmpresa.toString()));
    this.listaEmpresas[selectedIndex] = this.originalCopy;
    this._empresas$.next(this.listaEmpresas);
  }

  loadEmpresa(ebcodemp: number): Observable<Empresa> {
    this._loading$.next(true);
    return this.http.get<Empresa>(`${environment.serviceUrls.serviceUrl}/${ebcodemp}`)
      .pipe(
        finalize(() => this._loading$.next(false)),
        tap({
          next: _ => {
            this.log(`EmpresaService: Selecionado empresa com cÃ³digo=${ebcodemp}`)
          },
          error: error => {
            console.log('Error')
            catchError(this.handleError<Empresa>(`getEmpresa cÃ³digo=${ebcodemp}`))
          },
          complete: () => console.log('Completed')
        })
      );
  }

  include(empresa: Empresa, done): void {
    this._loading$.next(true);
    let retVal: Observable<Empresa> = this.http.put<Empresa>(`${environment.serviceUrls.serviceUrl}/`, empresa, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      catchError(this.handleError<Empresa>('Incluir'))
      //`Erro durante a inclusÃ£o do item cÃ³digo=${item['rowId']}`
      //this.errors = 'Ocorreu um erro ao tentar guardar o critÃ©rio!';
    ).subscribe(response => {
      if (response) {
        let msg = `Empresa de cÃ³digo ${response['entity']['codEmpresa']} salva com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");

        //this.listaItems.push(item);
        this.listaEmpresas.unshift(response['entity']);
        this._empresas$.next(this.listaEmpresas);
        done();
      };
    }, error => {
      let msg = `Ocorreu uma falha ao incluir Empresa  ${empresa.codEmpresa}. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });
  }

  save(empresas: Empresa[], done): void {
    this._loading$.next(true);
    let retVal: Observable<Empresa[]> = this.http.post<Empresa[]>(environment.serviceUrls.serviceUrl, empresas, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      catchError(this.handleError<Empresa[]>('Salvar'))
    ).subscribe(response => {
      if (response) {
        console.log(`${this._serviceName}: Foram atualizados ${empresas.length} items com sucesso!`);
        let msg = `Empresa alterada com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        this._empresas$.next(this.listaEmpresas);
        done();
      };
    }, error => {
      let msg = `Ocorreu uma falha ao atualizar Empresas. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });
  }

  /** DELETE: delete empresa from the server */
  remove(codEmpresa: number, done): void {
    this._loading$.next(true);
    let retVal: Observable<Empresa> = this.http.delete<Empresa>(`${environment.serviceUrls.serviceUrl}/${codEmpresa}`, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      catchError(this.handleError<Empresa>('Remover'))
      ).subscribe(response => {
        if (response){
          let itemIndex = this.listaEmpresas.findIndex((item) => item['codEmpresa'] === codEmpresa);
          if (itemIndex >= 0) {
            this.listaEmpresas.splice(itemIndex, 1);
            console.log(`Item [${codEmpresa}] removido com sucesso`);
          }
          let msg = `Item removido com sucesso!`;
          this.toastr.success(msg, "CyberMessenger");
          this._empresas$.next(this.listaEmpresas);
          done();
        }else{
          let msg = `Ocorreu uma falha ao remover Empresa ${codEmpresa}. Tente novemente!`;
          this.toastr.error(msg, "CyberMessenger");  
        }
        done();
      }, error => {
        let msg = `Ocorreu uma falha ao remover Empresa ${codEmpresa}. Tente novemente!`;
        this.toastr.error(msg, "CyberMessenger");  
        console.log('Erro ao remover registro: ' + error.error.message); 
      });
    
  }

  searchEmpresa(term: string): Observable<Empresa> {
    if (!term.trim()) {
      // if not search term, return empty hero array.
      this.loadEmpresas();
      return;
    }
    //const url = `${this.httpUrl}/${this.empresaUrl}/?term=${term}`;
    const url = `${environment.serviceUrls.serviceUrl}/?term=${term}`;
    return this.http.get<Empresa>(url).pipe(
      tap(_ => console.log(_)),
      catchError(this.handleError<Empresa>('searchEmpresa'))
    );
    //tap(_ => this.log(`found empresas matching "${term}"`)),
  }


  /**
  * Handle Http operation that failed.
  * Let the app continue.
  * @param operation - name of the operation that failed
  * @param result - optional value to return as the observable result
  */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
