import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { Empresa } from '../models/empresa.model';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { EmpresasService } from '../services/empresas.service';
//import { QuestionService } from '../question.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Subscription, throwError } from 'rxjs';

@Component({
  selector: 'empresas-edit',
  templateUrl: './empresas-edit.component.html',
  styleUrls: ['../empresas.component.scss']//,
  //providers:  [QuestionService]
})


export class EmpresasEditComponent implements OnInit, OnDestroy {
  empresa: Empresa;
  subtitle: string;
  resourceId: string;
  openType: string;
  content: Empresa;
  originalCopy: Empresa;

  goBackLink: string;
  errors: string;
  questions: any[];
  static LOCALE_KEY = 'currentLocale';

  private empresasSubs: Subscription;

  contentForm = new FormGroup({
    "codEmpresa": new FormControl('', [
      Validators.required,
      Validators.maxLength(4)
    ]),
    "nomeEmpresa": new FormControl('', [
      Validators.required,
      Validators.maxLength(50)
    ]),
    "cnpj": new FormControl('', [
      Validators.required,
      Validators.maxLength(20)
    ]),
    "numeroAgencia": new FormControl('', [
      Validators.required,
      Validators.maxLength(20)
    ]),
    "dvAgencia": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ]),
    "numeroConta": new FormControl('', [
      Validators.required,
      Validators.maxLength(20)
    ]),
    "dvConta": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ]),
    "instrucao1": new FormControl('', [
      Validators.required,
      Validators.maxLength(80)
    ]),
    "instrucao2": new FormControl('', [
      Validators.required,
      Validators.maxLength(80)
    ]),
    "instrucao3": new FormControl('', [
      Validators.required,
      Validators.maxLength(80)
    ]),
    "instrucao4": new FormControl('', [
      Validators.required,
      Validators.maxLength(80)
    ]),
    "instrucao5": new FormControl('', [
      Validators.required,
      Validators.maxLength(80)
    ]),
    "fxContInicial": new FormControl('', [
      Validators.required,
      Validators.maxLength(13)
    ]),
    "fxContFinal": new FormControl('', [
      Validators.required,
      Validators.maxLength(13)
    ]),
    "contBoleto": new FormControl('', [
      Validators.required,
      Validators.maxLength(13)
    ]),
    "tipoDoc": new FormControl('', [
      Validators.required,
      Validators.maxLength(20)
    ]),
    "aceite": new FormControl('', [
      Validators.required,
      Validators.maxLength(20)
    ]),
    "local1": new FormControl('', [
      Validators.required,
      Validators.maxLength(80)
    ]),
    "local2": new FormControl('', [
      Validators.required,
      Validators.maxLength(80)
    ]),
    "numeroConvenio": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "carteira": new FormControl('', [
      Validators.required,
      Validators.maxLength(20)
    ]),
    "nomeBanco": new FormControl('', [
      Validators.required,
      Validators.maxLength(15)
    ]),
    "numBanco": new FormControl('', [
      Validators.required,
      Validators.maxLength(3)
    ]),
    "dvBanco": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ])
  });

  constructor(private route: ActivatedRoute,
    private router: Router,
    private empresaService: EmpresasService,
    private toastr: ToastrService,
    public translate: TranslateService//,
    //service: QuestionService,
  ) {

    //this.questions = service.getQuestions();

    translate.setDefaultLang('ptBR');
    const currentLocale = sessionStorage.getItem(EmpresasEditComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);
    this.goBackLink = '/external-' + this.empresaService.getResourceName();
    
  }

  ngOnInit() {
    this.resourceId = this.route.snapshot.paramMap.get('code');
    this.openType = this.route.snapshot.paramMap.get('openType');
    this.subtitle = 'EMPRESAS.ITEMSUBTITLE.' + this.openType.toUpperCase();

    if (this.resourceId === null || this.resourceId == undefined)
      this.empresasSubs = this.empresaService.startNewItem()
              .subscribe(faseCobranca => {
                this.content = faseCobranca;
                this.contentForm.patchValue(this.content);
              });
    else
      this.empresasSubs = this.empresaService.selectItem(this.resourceId)
              .subscribe(item => {
                this.content = (item === null) ? this.content : item;
                this.contentForm.patchValue(this.content);
              });

    this.errors = '';
  }

  ngOnDestroy(){
    console.log('Cleaning Empresas...');
    if (this.empresasSubs != undefined) this.empresasSubs.unsubscribe();
  }

  getEmpresa(): void {
    const ebcodemp = +this.route.snapshot.paramMap.get('ebcodemp');
    this.empresaService.loadEmpresa(ebcodemp)
      .subscribe(empresas => this.empresa = empresas[0]);
  }

  save() {
    switch (this.openType) {
      case 'remove':
        this.empresaService.remove(this.content.codEmpresa, () => this.exit());
        break;
      case 'change':
        this.empresaService.save([this.content], () => this.exit()); 
        break;
      case 'create':
        if (this.empresaService.validItem(this.content.codEmpresa.toString()) === 0) {
          this.empresaService.include(this.content, () => this.exit());
        } else {
          let msg = `JÃ¡ existe uma empresa cadastrada com este cÃ³digo`;
          this.toastr.error(msg, "Erro:");
        }
        break;

      default:
        break;
    }
  }
  cancel() {
    this.empresaService.rollbackChanges();
    this.exit();
  }

  exit(): void {
    this.router.navigate([this.goBackLink]);
  }


  onChange($event) {
    console.log(this.content.aceite);
  }

  get diagnostic() { return JSON.stringify(this.content); }

}
