//import { BrowserModule } from '@angular/platform-browser';
import { CommonModule, DecimalPipe } from '@angular/common';
import { NgModule } from '@angular/core';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import {TranslateModule, TranslateLoader, MissingTranslationHandler} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';

import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { NgbModule,NgbPaginationModule, NgbTypeaheadModule,NgbButtonLabel } from '@ng-bootstrap/ng-bootstrap';
import { ToastrModule } from 'ngx-toastr';

import { EmpresasListComponent } from './list/empresas-list.component';
import { EmpresasEditComponent } from './editor/empresas-edit.component';

//import { EmpresasRoutingModule } from './empresas-routing.module';
import { NgbdSortableHeader } from './directives/sortable.directive';
import { EmpresasComponent } from './empresas.component';
import { EmpresasRoutingModule } from './empresas-routing.module';
import { PPWMissingTranslationHandler } from './util/missingTranslationHandler';
import { EmpresasService } from './services/empresas.service';
import { DynamicFormComponent }         from './dynamic-form.component';
import { DynamicFormQuestionComponent } from './dynamic-form-question.component';
import { PPWInterceptorProviders } from './interceptors/ppw.interceptor.provider';

import { environment } from '../environments/environment';


// imports para o teste com o form // 



//import { environment } from 'src/environments/environment';

//const localeStartingURL = environment.localeUrlFolder;
//const localeFileType = environment.localeFileType;

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, environment.localeUrlFolder, environment.localeFileType);
}
@NgModule({
  declarations: [
    EmpresasComponent,
    NgbdSortableHeader,
    EmpresasListComponent,
    EmpresasEditComponent,
    DynamicFormComponent,
    DynamicFormQuestionComponent],
  imports: [
      CommonModule,
      FontAwesomeModule,
      FormsModule,
      ReactiveFormsModule,
      HttpClientModule,
      NgbPaginationModule,
      NgbTypeaheadModule,
      NgbModule,
      ToastrModule.forRoot() ,
      TranslateModule.forChild({
        missingTranslationHandler: {provide: MissingTranslationHandler, useClass: PPWMissingTranslationHandler},
        loader: { provide: TranslateLoader, useFactory: HttpLoaderFactory, deps: [HttpClient]},
          isolate: true
      }),
      EmpresasRoutingModule
    ],
    providers:[PPWInterceptorProviders,  NgbButtonLabel,EmpresasService, DecimalPipe],
    bootstrap: [EmpresasComponent,EmpresasEditComponent]
})
export class EmpresasModule{}
