import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { EmpresasComponent } from './empresas.component';
import { EmpresasListComponent } from './list/empresas-list.component';
import { EmpresasEditComponent } from './editor/empresas-edit.component';
import { ProjectConfigResolver } from './interceptors/project.config.resolver';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: EmpresasComponent,
          children: [
            {path: '', component: EmpresasListComponent},
            {path: ':openType',component: EmpresasEditComponent},
            {path: ':openType/:code',component: EmpresasEditComponent }
          ],resolve: {
            envConfiguration: ProjectConfigResolver
          }}
    ])
  ],
  exports: [RouterModule]
})
export class EmpresasRoutingModule { }